<template>
    <div class="header">
        <img class="img" src="../../assets/img/白色的的.jpg" alt="">
        <el-menu router :default-active="activeIndex" class="el-menu-demo" mode="horizontal"">
             <el-menu-item   v-for=" (item,index) in headerTabList" :key="index" :index="item.index">
            {{item.name}}
            </el-menu-item>
        </el-menu>
        <div class="home" @click="menuChange">
            <el-dropdown trigger="click">
                <span class="el-dropdown-link" style="line-height: 80px;">
                    {{mobileTitle}}<i class="el-icon-arrow-down el-icon--right"></i>
                </span>
                <el-dropdown-menu slot="dropdown">
                    <el-dropdown-item @click.native="handleCommand(index)" :command="item.index"
                        v-for="(item,index) in headerTabList" :key="index">{{item.name}}</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </div>
    </div>
</template>



<script>
    import { headerTabList } from './index.js'
    export default {
        components: {

        },
        data() {
            return {
                activeIndex: '',
                headerTabList: headerTabList,
                url: 'https://fuss10.elemecdn.com/e/5d/4a731a90594a4af544c0c25941171jpeg.jpeg',
                isCollapse: true,
                mobileTitle: '首页'
            }
        },
        mounted() {
            this.activeIndex = this.$route.path
            this.headerTabList.map(item=>{
                if(item.index===this.$route.path){
                    this.mobileTitle=item.name
                }
            })
        },
        methods: {
            menuChange() {
                this.isCollapse = !this.isCollapse
            },
            handleCommand(index) {
                console.log('handleCommand')
                this.mobileTitle = this.headerTabList[index].name
                this.$router.push(this.headerTabList[index].index)
            }
        },
    }
</script>


<style lang="scss" scoped>
    div /deep/.el-menu.el-menu--horizontal {
        border-bottom: 0;
    }

    .img {
        height: 70px;
    }

    .header {
        background-color: #FFFFFF;
    }

    .home {
        display: none;
    }

    .el-menu-demo {
        display: block;
    }

    @media not screen and (min-width: 45em) {
        .home {
            display: block;
        }

        .el-menu-demo {
            display: none;
        }
        .el-dropdown-link{
            cursor: pointer;
    color: #409eff;
    font-size: 18px;
        }
    }
</style>