import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home'//首页
import recommend from '../views/recommend' //公司介绍
import news from '../views/news' //新闻动态
import service from '../views/service' //业务板块
import finance from '../views/finance' //金融项目
import business_qualification from '../views/business_qualification' //营业资质
import contact from '../views/contact' //联系我们
import download from '../views/download' //app下载
// import download from '../views/service/components/info.vue' //app下载

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/recommend',
    name: 'Home',
    component: recommend
  },
  {
    path: '/news',
    name: 'Home',
    component: news
  },
  {
    path: '/service',
    name: 'Home',
    component: service
  },
  {
    path: '/finance',
    name: 'Home',
    component: finance
  },
  {
    path: '/finance/info',
    name: 'Home',
    component: () => import(/* webpackChunkName: "about" */ '../views/finance/components/info.vue')
  },
  {
    path: '/business_qualification',
    name: 'Home',
    component: business_qualification,
  },
  {
    path: '/contact',
    name: 'Home',
    component: contact
  },
  {
    path: '/download',
    name: 'Home',
    component: download
  },
  {
    path: '/server/info',
    name: 'serverinfo',
    component: () => import(/* webpackChunkName: "about" */ '../views/service/components/info.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
